import { Component } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

@Component({
  selector: 'app-precio-form',
  standalone: true,
  imports: [CommonModule, FormsModule],
  templateUrl: './precio-form.component.html',
  styleUrls: ['./precio-form.component.scss']
})
export class PrecioFormComponent {
  // Modelo del formulario
  producto = {
    codigoProveedor: '',
    codigoSap: '',
    descripcion: '',
    unidadEmpaque: '',
    costoUnitario: 0,
    descuento1: 0,
    descuento2: 0,
    costoFinal: 0,
    variacionCosto: 0,
    observaciones: '',
    precioVentaSugerido: 0
  };

  // Estado del formulario
  isLoading = false;
  submitted = false;

  calcularCostoFinal() {
    const desc1 = this.producto.descuento1 / 100;
    const desc2 = this.producto.descuento2 / 100;
    this.producto.costoFinal = this.producto.costoUnitario * (1 - desc1) * (1 - desc2);
    return this.producto.costoFinal;
  }

  onSubmit() {
    this.submitted = true;
    this.isLoading = true;
    
    // Simular envío a API
    setTimeout(() => {
      console.log('Producto registrado:', this.producto);
      this.isLoading = false;
      // this.resetForm(); // Opcional: limpiar formulario después de enviar
    }, 1000);
  }

  resetForm() {
    this.producto = {
      codigoProveedor: '',
      codigoSap: '',
      descripcion: '',
      unidadEmpaque: '',
      costoUnitario: 0,
      descuento1: 0,
      descuento2: 0,
      costoFinal: 0,
      variacionCosto: 0,
      observaciones: '',
      precioVentaSugerido: 0
    };
    this.submitted = false;
  }
}